import { useState, useEffect } from "react";

function usePreloadImages(imageArray, fontArray) {
  const [isLoading, setIsLoading] = useState(true);
  const [loadedImages, setLoadedImages] = useState(0);

  useEffect(() => {
    const totalImages = imageArray.length + fontArray.length;

    if (totalImages === 0) {
      //setIsLoading(false);
      return;
    }

    console.log(totalImages);

    const preloadImage = (src) => {
      const img = new Image();
      img.src = src;

      //console.log(img);

      img.onload = () => {
        //console.log(`Successfully loaded image: ${src}`);
        setLoadedImages((prevCount) => prevCount + 1);
      };

      img.onerror = () => {
        console.error(`Failed to load image: ${src}`);
        setLoadedImages((prevCount) => prevCount + 1);
      };
    };

    const preloadFont = (font) => {
      const fontFace = new FontFace(font.name, `url(${font.url})`);
      fontFace
        .load()
        .then(() => {
          console.log(`Successfully loaded font: ${font.name}`);
          document.fonts.add(fontFace);
          setLoadedImages((prevCount) => prevCount + 1);
        })
        .catch((error) => {
          console.error(`Failed to load font: ${font.name}`, error);
          setLoadedImages((prevCount) => prevCount + 1); // Consider it loaded to avoid infinite loading state
        });
    };

    imageArray.forEach(preloadImage);
    fontArray.forEach(preloadFont);
  }, [imageArray, fontArray]);

  useEffect(() => {
    // Once all images are loaded, set the loading state to false
    if (
      loadedImages === imageArray.length + fontArray.length &&
      imageArray.length + fontArray.length > 0
    ) {
      console.log("All images loaded.");
      setIsLoading(false);
    }
  }, [loadedImages, imageArray.length, fontArray.length]);

  return isLoading;
}

export { usePreloadImages };
